    generateDetail(data) {
        return (
            <div className="card">

                <img src={person1} width={'200px'} height={'150px'} className='person-image1' />
                <span className='name'> {data.Name}</span>
                <span className="btn"> <Link to='/EmployeeDetail' onClick={() => this.dispatch(data)} className='button'>view</Link></span>
                <p className='idno'>{data.EmployeeID}</p>

            </div>
        );
    }
    displayDetail() {

        return this.employeeData.map(eachdata => this.generateDetail(eachdata));
    }
    dispatch = (employee) => {
        this.props.dispatch({ type: setEmployee, payload: employee })

    }

    render() {
        return (
            <React.Fragment>
                {this.displayDetail()}
            </React.Fragment>
        )
    }
